// src/components/projects/ProjectMatchingForm.jsx
import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Brain, Loader2 } from "lucide-react";

export function ProjectMatchingForm({ onSubmit, isAnalyzing }) {
  const [formData, setFormData] = useState({
    title: "",
    description: "",
    type: "",
    teamSize: "",
    duration: "",
    keySkills: "",
    additionalInfo: "",
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit(formData);
  };

  const StepIndicator = ({ step, currentStep, title }) => (
    <div className="flex items-center gap-3">
      <div
        className={`w-8 h-8 rounded-full flex items-center justify-center ${
          currentStep >= step
            ? "bg-gradient-to-r from-red-900 to-red-500 text-white"
            : "bg-slate-100 dark:bg-slate-800 text-slate-500"
        }`}
      >
        {step}
      </div>
      <span
        className={
          currentStep >= step ? "text-foreground" : "text-muted-foreground"
        }
      >
        {title}
      </span>
    </div>
  );

  return (
    <form onSubmit={handleSubmit} className="space-y-8">
      <div className="flex items-center justify-between mb-8">
        <StepIndicator step={1} currentStep={1} title="Project Details" />
        <div className="h-1 flex-1 mx-4 bg-slate-100 dark:bg-slate-800">
          <div
            className="h-full bg-gradient-to-r from-red-900 to-red-500 rounded-full transition-all duration-500"
            style={{ width: isAnalyzing ? "100%" : "33%" }}
          />
        </div>
        <StepIndicator
          step={2}
          currentStep={isAnalyzing ? 2 : 1}
          title="AI Analysis"
        />
        <div className="h-1 flex-1 mx-4 bg-slate-100 dark:bg-slate-800">
          <div
            className="h-full bg-gradient-to-r from-red-900 to-red-500 rounded-full transition-all duration-500"
            style={{ width: isAnalyzing ? "100%" : "0%" }}
          />
        </div>
        <StepIndicator
          step={3}
          currentStep={isAnalyzing ? 2 : 1}
          title="Results"
        />
      </div>

      <div className="grid gap-6">
        <div className="space-y-2">
          <Label>Project Title</Label>
          <Input
            placeholder="Enter your project title"
            value={formData.title}
            onChange={(e) =>
              setFormData({ ...formData, title: e.target.value })
            }
          />
        </div>

        <div className="space-y-2">
          <Label>Project Description</Label>
          <Textarea
            placeholder="Describe your project in detail. Include goals, challenges, and desired outcomes..."
            className="h-32"
            value={formData.description}
            onChange={(e) =>
              setFormData({ ...formData, description: e.target.value })
            }
          />
        </div>

        <div className="grid grid-cols-2 gap-4">
          <div className="space-y-2">
            <Label>Project Type</Label>
            <Select
              value={formData.type}
              onValueChange={(value) =>
                setFormData({ ...formData, type: value })
              }
            >
              <SelectTrigger>
                <SelectValue placeholder="Select project type" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="tech">Technology</SelectItem>
                <SelectItem value="research">Research</SelectItem>
                <SelectItem value="business">Business</SelectItem>
                <SelectItem value="social">Social Impact</SelectItem>
                <SelectItem value="creative">Creative</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label>Team Size</Label>
            <Select
              value={formData.teamSize}
              onValueChange={(value) =>
                setFormData({ ...formData, teamSize: value })
              }
            >
              <SelectTrigger>
                <SelectValue placeholder="Select team size" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="2-3">2-3 members</SelectItem>
                <SelectItem value="4-6">4-6 members</SelectItem>
                <SelectItem value="7-10">7-10 members</SelectItem>
                <SelectItem value="10+">10+ members</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </div>

        <div className="grid grid-cols-2 gap-4">
          <div className="space-y-2">
            <Label>Duration</Label>
            <Select
              value={formData.duration}
              onValueChange={(value) =>
                setFormData({ ...formData, duration: value })
              }
            >
              <SelectTrigger>
                <SelectValue placeholder="Select duration" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="1-3">1-3 months</SelectItem>
                <SelectItem value="3-6">3-6 months</SelectItem>
                <SelectItem value="6-12">6-12 months</SelectItem>
                <SelectItem value="12+">12+ months</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label>Key Skills Required (comma-separated)</Label>
            <Input
              placeholder="e.g., Python, Machine Learning, UI Design"
              value={formData.keySkills}
              onChange={(e) =>
                setFormData({ ...formData, keySkills: e.target.value })
              }
            />
          </div>
        </div>

        <div className="space-y-2">
          <Label>Additional Information</Label>
          <Textarea
            placeholder="Any other details that might help in finding the perfect team..."
            value={formData.additionalInfo}
            onChange={(e) =>
              setFormData({ ...formData, additionalInfo: e.target.value })
            }
          />
        </div>
      </div>

      <Button
        type="submit"
        className="w-full bg-gradient-to-r from-red-900 to-red-500 text-white"
        disabled={isAnalyzing}
      >
        {isAnalyzing ? (
          <>
            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
            Analyzing Project Requirements...
          </>
        ) : (
          <>
            <Brain className="mr-2 h-4 w-4" />
            Start AI Analysis
          </>
        )}
      </Button>
    </form>
  );
}