import {
  ResponsiveContainer,
  RadarChart,
  PolarGrid,
  PolarAngleAxis,
  PolarRadiusAxis,
  Radar,
  Legend,
} from "recharts";

export function SkillsGraph() {
  const data = [
    {
      subject: "Technical Skills",
      required: 90,
      available: 95,
      gap: 0,
    },
    {
      subject: "Domain Knowledge",
      required: 80,
      available: 85,
      gap: 0,
    },
    {
      subject: "Leadership",
      required: 70,
      available: 80,
      gap: 0,
    },
    {
      subject: "Communication",
      required: 85,
      available: 90,
      gap: 0,
    },
    {
      subject: "Problem Solving",
      required: 95,
      available: 92,
      gap: 3,
    },
    {
      subject: "Innovation",
      required: 75,
      available: 88,
      gap: 0,
    },
  ];

  return (
    <ResponsiveContainer width="100%" height="100%">
      <RadarChart cx="50%" cy="50%" outerRadius="80%" data={data}>
        <PolarGrid
          strokeDasharray="3 3"
          className="stroke-slate-200 dark:stroke-slate-700"
        />
        <PolarAngleAxis
          dataKey="subject"
          tick={{ fill: "hsl(var(--foreground))", fontSize: 12 }}
        />
        <PolarRadiusAxis angle={30} domain={[0, 100]} />
        <Radar
          name="Required Skills"
          dataKey="required"
          stroke="#3b82f6"
          fill="#3b82f6"
          fillOpacity={0.1}
        />
        <Radar
          name="Available Skills"
          dataKey="available"
          stroke="#8b5cf6"
          fill="#8b5cf6"
          fillOpacity={0.1}
        />
        <Legend />
      </RadarChart>
    </ResponsiveContainer>
  );
}
