// src/components/projects/ProjectCard.jsx
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Progress } from "@/components/ui/progress";
import { Users, Clock, Activity, ArrowRight } from "lucide-react";

export function ProjectCard({ project, onSelect }) {
  const getStatusColor = (status) => {
    switch (status.toLowerCase()) {
      case "active":
        return "bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-100";
      case "planning":
        return "bg-red-100 dark:bg-red-900 text-red-700 dark:text-red-100";
      case "completed":
        return "bg-red-100 dark:bg-red-900 text-red-700 dark:text-red-100";
      default:
        return "bg-slate-100 dark:bg-slate-800";
    }
  };

  return (
    <Card className="hover:shadow-lg transition-shadow relative overflow-hidden">
      {/* Match Score Ribbon */}
      <div className="absolute top-0 right-0 w-24 h-24">
        <div className="absolute transform rotate-45 bg-gradient-to-r from-red-900 to-red-500 text-white text-sm font-semibold text-center w-32 top-6 -right-8">
          {project.matchScore}% Match
        </div>
      </div>

      <CardHeader>
        <div className="flex items-start justify-between">
          <div className="space-y-1">
            <CardTitle className="pr-16">{project.title}</CardTitle>
            <CardDescription className="flex items-center gap-2">
              <Badge
                variant="secondary"
                className={getStatusColor(project.status)}
              >
                {project.status}
              </Badge>
              <span>â€¢</span>
              <span>{project.type}</span>
            </CardDescription>
          </div>
        </div>
      </CardHeader>

      <CardContent className="space-y-4">
        <p className="text-sm text-muted-foreground">{project.description}</p>

        <div className="flex flex-wrap gap-2">
          {project.requiredSkills.map((skill, index) => (
            <Badge
              key={index}
              variant="outline"
              className="bg-slate-100 dark:bg-slate-800"
            >
              {skill}
            </Badge>
          ))}
        </div>

        <div className="grid grid-cols-3 gap-4">
          <div className="space-y-1">
            <div className="text-sm text-muted-foreground flex items-center">
              <Users className="mr-2 h-4 w-4" />
              Team Size
            </div>
            <p className="font-medium">{project.teamSize}</p>
          </div>
          <div className="space-y-1">
            <div className="text-sm text-muted-foreground flex items-center">
              <Clock className="mr-2 h-4 w-4" />
              Duration
            </div>
            <p className="font-medium">{project.duration}</p>
          </div>
          <div className="space-y-1">
            <div className="text-sm text-muted-foreground flex items-center">
              <Activity className="mr-2 h-4 w-4" />
              Complexity
            </div>
            <p className="font-medium">{project.complexity}</p>
          </div>
        </div>

        <div className="pt-4 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <Avatar>
              <AvatarImage src={project.leader.image} />
              <AvatarFallback>
                {project.leader.name
                  .split(" ")
                  .map((n) => n[0])
                  .join("")}
              </AvatarFallback>
            </Avatar>
            <div className="text-sm">
              <p className="font-medium">{project.leader.name}</p>
              <p className="text-muted-foreground">{project.leader.role}</p>
            </div>
          </div>
          <Button
            className="bg-gradient-to-r from-red-900 to-red-500 text-white"
            onClick={() => onSelect(project)}
          >
            View Details
            <ArrowRight className="ml-2 h-4 w-4" />
          </Button>
        </div>
      </CardContent>
    </Card>
  );
}
